.ai-chat-container {
    transition: opacity 0.3s ease-out, height 0.3s ease-out, box-shadow 0.3s ease-out;

    .ai-chat-frame {
        transition: box-shadow 0.3s ease-out, transform 0.3s ease-out;

        iframe {
            transition: opacity 0.15s ease-out 0.15s;
        }
    }

    .ai-chat-close {
        transition: transform 0.2s ease-out 0.2s, opacity 0.2s ease-out 0.2s;
    }

    // iframe {
    //     transition: transform 0.3s ease-out;
    // }
    // &.shown {
    // animation: ai-chat-opening 0.3s ease-out alternate;
    // }
}

@media (max-width: 379px) or (max-height: 529px) {
    .ai-chat-container {
        display: flex;
        align-items: center;
        justify-content: center;
        box-sizing: border-box;
        padding: 5vw;
        width: 100% !important;
        height: 100% !important;
        background-color: rgba(#000, 0.6);

        .ai-chat-frame {
            width: 100% !important;
            transition: box-shadow 0.1s ease-out, transform 0.1s ease-out;
        }

        .ai-chat-close {
            z-index: 0 !important;
            left: initial !important;
            top: calc(5vw + 19px) !important;
            right: calc(5vw + 20px);
        }

        &.opening,
        &.opened,
        &.closing {
            inset: 0 !important;
        }
    }

    body:has(.ai-chat-container.opening),
    body:has(.ai-chat-container.opened) {
        overflow: hidden;
    }
}